:root{--primaryColor: #e03e0e;--primaryColor2: #fa8717;--primaryColor3: #fee6cf;--bannerColor: #ea5023;--fontFamily: "Nunito", sans-serif;--templateColor: #e03e0e;--templateColor2: #ff8a73;--whiteColor: #ffffff;--lightColor: #f8f8fc;--darkColor: #7c7c80;--blackColor: #000000;--paragraphColor: #818992;--transition: 0.5s;--darkColor1: #474747;--blue: #4a50ff;--blueActive: #7d81f4;--pink: #d74a96;--pinkHover: #f076b8;--lightBlue: #6195fd;--lightBlueHover: #1763fb;--mint: #24b6b1;--mintHover: #2c9491;--green: #47821c;--greenHover: #5bb01c;--purpule: #7b70ff;--purpleHover: #5145e2;--yellow: #ffc736;--yellowHover: #fbb911;--breadcrumbtextcolor: #d1c4e9;--breadcrumbtextcoloractive: #fff;--lightOrange: #ffc61a;--orange1: #f16c1c}.rules__page .rules-area{margin-top:110px;margin-bottom:110px;width:1000px;margin-left:auto;margin-right:auto}.rules__page .rules-area .event-calendar-container{background:var(--lightOrange);border-radius:40px;padding:50px;display:flex;flex-direction:column;align-items:center}.rules__page .rules-area .event-calendar-container .calendar-title{font-weight:bold;font-size:46px;line-height:55px;text-align:center;color:#fff}.rules__page .rules-area .event-calendar-container .calendar-text-black{font-weight:500;font-size:18px;line-height:22px;text-align:center;letter-spacing:-0.005em;color:#1e3d74}.rules__page .rules-area .event-calendar-container .calendar-icon__container{position:relative;width:100%}.rules__page .rules-area .event-calendar-container .calendar-icon__container .calender-coin-icon{width:124px;max-height:115px;position:absolute;bottom:0px;right:-20%}.rules__page .rules-area .event-calendar-container .calendar-icon__container .calendar-date{position:absolute;font-size:4rem;color:#fff;font-weight:800;top:15%;right:20%;letter-spacing:3px}.rules__page .rules-area .event-calendar-container .calendar-icon__container .calendar-month{position:absolute;font-size:2rem;color:#fff;font-weight:800;bottom:10%;right:26%;letter-spacing:3px}.rules__page .rules-area .event-calendar-container .notic{font-weight:500;font-size:24px;line-height:28px;text-align:left;letter-spacing:-0.005em;color:#1e3d74;width:100%}.rules__page .rules-area .event-calendar-container .notic-text-black{font-weight:600;font-size:18px;line-height:22px;letter-spacing:-0.005em;color:#1e3d74;width:100%;text-align:left}.rules__page .rules-area .event-calendar-container .notic-text-white{font-weight:700;font-size:18px;line-height:22px;letter-spacing:-0.005em;color:#fff}.rules__page .rules-area .event-calendar-container .social-link{display:flex;flex-direction:row;width:100%;justify-content:flex-start;align-items:center;margin-bottom:14px;cursor:pointer}.rules__page .rules-area .event-calendar-container .social-link:hover{transform:translateX(5px);transition:transform 0.2s ease-in-out}.rules__page .rules-area .event-calendar-container .social-link>p{font-style:normal;font-weight:bold;font-size:18px;line-height:22px;color:#1e3d74;margin-left:8px}.rules__page .rules-area .rules-detail-container{background:#ffffff;border:5px solid #f2f2f2;border-radius:40px;padding:150px 50px 50px 50px;position:relative}.rules__page .rules-area .rules-detail-container .rules-detail__title-container{position:absolute;top:-70px;left:50%;transform:translate(-50%)}.rules__page .rules-area .rules-detail-container .rules-details_list>li{font-size:24px;line-height:28px;font-weight:800;color:#1e3d74;margin-bottom:24px}.rules__page .rules-area .rules-detail-container .rules-details_list>li .note{color:var(--primaryColor);font-size:18px;line-height:26px;font-weight:500}.rules__page .rules-area .rules-detail-container .rules-details_list>li>div{font-size:18px;line-height:26px;font-weight:500;color:#1e3d74}.rules__page .rules-area .rules-detail-container .rules-details_list>li>ul>li{list-style:disc;font-size:18px;line-height:26px;font-weight:500;color:#1e3d74}.rules__page .rules-area .rules-detail-container .rules-details_list .rank-table-title{font-style:normal;font-weight:bold;font-size:18px;line-height:22px;text-align:center;color:#f68317;margin-bottom:8px}.rules__page .rules-area .rules-detail-container .rules-detail__content ul{padding-left:0}.rules__page .rules-area .rules-detail-container .rules-detail__content ul li{list-style-type:disc;font-size:18px;line-height:24px;color:#1e3d74;font-weight:500}.rules__page .rules-area .rules-detail-container .rules-detail__content ul .rule-title{font-size:24px;line-height:28px;font-weight:800;color:#1e3d74;list-style:decimal;padding-left:0;margin-top:20px}.rules__page .rules-area .rules-detail-container .rules-detail__content ul div{font-weight:500}.rules__page .rules-area .rules-detail-container .rules-detail__content ul>li>ul{padding-left:26px}.rules__page .rules-area .rules-detail-container .rules-details-button-wrapper{position:relative;display:flex;justify-content:center;align-items:center;margin-bottom:20px;margin-top:10px;cursor:pointer}.rules__page .rules-area .rules-detail-container .rules-details-button-wrapper:active{transform:scale(0.9);transition:transform 0.1s ease-in-out}.rules__page .rules-area .rules-detail-container .rules-details-button-wrapper .rules-details-button-text{user-select:none;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);font-weight:500;font-size:18px;line-height:22px;text-align:center;color:#ffffff}
